import React, { Component, Fragment } from 'react';
import { Tab, Nav } from 'react-bootstrap';
import moment from 'moment';
import PerfectScrollbar from 'react-perfect-scrollbar'
import { Link } from 'react-router-dom';
import { baseUrl } from '../../../../Assets/AssetsHelper';
import { getNotificationList, notificationUpdateStatus } from '../../../../Services/Actions/homeActions'

class Notifications extends Component {
    constructor(props) {
        super(props)
        this.state = { notifications: props.notificationList }
    }
    updateStatus = (postId, status) => event => {
        var self = this;
        notificationUpdateStatus(postId, status).then((response) => {
            self.onLoad()
        })
    }

    render() {
        const { notifications } = this.state;

        return <div className="w-100">
            <Tab.Container id="notifications_tab_panel" defaultActiveKey="all-notifications">
                <div className="d-flex align-items-center justify-content-between px-3 pt-2 border-bottom">
                    <Nav variant="default" className="nav">
                        <Nav.Item className="">
                            <Nav.Link className="me-3 px-0" eventKey="all-notifications">All</Nav.Link>
                        </Nav.Item>
                        <Nav.Item className="">
                            <Nav.Link className="px-0" eventKey="unread-notifications">Unread</Nav.Link>
                        </Nav.Item>
                    </Nav>
                    <div>
                        <button type="button" className="btn btn-link btn-sm px-0 h-auto fw-bold text-decoration-none btn-active-color-primary">Mark all as read</button>
                    </div>
                </div>
                <PerfectScrollbar options={{ wheelPropagation: false }}>
                    {notifications && Object.keys(notifications).length > 0 ? <Tab.Content>
                        <Tab.Pane eventKey="all-notifications" className="pb-3">
                            {
                                notifications && Object.keys(notifications).map((iNot, index) => {
                                    return <Fragment key={index}><div className="d-flex align-items-center justify-content-between px-3 py-1" >
                                        <span className="text-gray-800 fw-bold">{iNot}</span>
                                        <span className="bg-primary badge rounded-5 min-w-30 text-light-alt d-none">{(notifications[iNot] && notifications[iNot].length > 0) ? notifications[iNot].length : 0}</span>
                                    </div>
                                        {notifications[iNot] && notifications[iNot].length > 0 ? <div className="">

                                            {
                                                notifications[iNot].map((imNot) => {
                                                    return <div key={imNot.id} className={`px-3 d-flex align-items-start border-bottom notification-item  ${imNot.status == 0 ? `bg-light` : ``}`}>
                                                        <div className="symbol symbol-38 rounded-1 overflow-hidden me-2 bg-light p-2">
                                                            <img alt="Pic" src={baseUrl("media/icons/social-icons/twitter.svg")} height="40" width="40" />
                                                        </div>
                                                        <div className="d-flex align-items-start flex-column flex-grow-1">
                                                            <div className="d-flex algin-items-center justify-content-between w-100">
                                                                <Link to={'/home'} className="text-hover-primary text-gray-800 fw-bold text-decoration-none me-2">{imNot.message.subject}</Link>
                                                                <span className="text-gray-500 fs-9">{moment(new Date(imNot.message.time * 1000)).format("hh:mma")}</span>
                                                            </div>
                                                            <div className="d-flex algin-items-start">
                                                                <span className="text-gray-600 fs-9">
                                                                    {imNot.message.text}
                                                                </span>
                                                            </div>
                                                        </div>
                                                    </div>

                                                })
                                            }

                                        </div> : null}
                                    </Fragment>

                                })
                            }
                        </Tab.Pane>
                        <Tab.Pane eventKey="unread-notifications" className="pb-3">
                            {
                                notifications && Object.keys(notifications).map((iNot, index) => {
                                    return notifications[iNot] && notifications[iNot].length > 0 && notifications[iNot].filter((a) => a.status == 0).length > 0 ? <><div key={index} className="d-flex align-items-center justify-content-between px-3 py-1">
                                        <span className="text-gray-800 fw-bold">{iNot}</span>
                                        <span className="bg-primary badge rounded-5 min-w-30 text-light-alt d-none">{notifications[iNot] && notifications[iNot].length > 0 && notifications[iNot].filter((a) => a.status == 0).length > 0 ? notifications[iNot].filter((a) => a.status == 0).length : 0}</span>
                                    </div>
                                        {notifications[iNot] && notifications[iNot].length > 0 ? <div className="">

                                            {
                                                notifications[iNot].map((imNot) => {
                                                    return imNot.status == 0 ? <div key={imNot.id} className="px-3 d-flex align-items-start border-bottom notification-item bg-light" onClick={this.updateStatus(imNot.id, 1)}>
                                                        <div className="symbol symbol-38 rounded-1 overflow-hidden me-2 bg-light p-2">
                                                            <img alt="Pic" src={baseUrl("media/icons/social-icons/twitter.svg")} height="40" width="40" />
                                                        </div>
                                                        <div className="d-flex align-items-start flex-column flex-grow-1">
                                                            <div className="d-flex algin-items-center justify-content-between w-100">
                                                                <Link to={'/home'} className="text-hover-primary text-gray-800 fw-bold text-decoration-none me-2">{imNot.message.subject}</Link>
                                                                <span className="text-gray-500 fs-9">{moment(new Date(imNot.message.time * 1000)).format("hh:mma")}</span>
                                                            </div>
                                                            <div className="d-flex algin-items-start">
                                                                <span className="text-gray-600 fs-9">
                                                                    {imNot.message.text}
                                                                </span>
                                                            </div>
                                                        </div>
                                                    </div> : null

                                                })
                                            }

                                        </div> : null}
                                    </> : null


                                })
                            }
                        </Tab.Pane>
                    </Tab.Content> : null}
                </PerfectScrollbar>
            </Tab.Container>
        </div>
    }
}

export default Notifications